@media screen and (min-width: 320px), (max-width: 480) {
  .container {
    min-width: 320px;
    max-width: 428px;
    margin: auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}







